import { LoginGruard } from '../Authority/loginguard';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DefinedTestEditComponent } from './definedTestEdit/definedTestEdit.component';
import { DefinedTestMachineAssignmentComponent } from './definedTestMachineAssignment/definedTestMachineAssignment.component';
import { DefinedTestGroupAssignmentComponent } from './definedTestGroupAssignment/definedTestGroupAssignment.component';
import { DefinedTestMachineAssignmentEditComponent } from './definedTestMachineAssignmentEdit/definedTestMachineAssignmentEdit.component';
import { DefinedTestGroupAssignmentEditComponent } from './definedTestGroupAssignmentEdit/definedTestGroupAssignmentEdit.component';
import { DefinedTestComponent } from './definedTest/definedTest.component';

const routes: Routes = [
  {
    path: '',
    children: [
      { path: '', component: DefinedTestComponent, data: { breadcrumb: '   ', pageTitle: "  " } },
      { path: 'Create', component: DefinedTestEditComponent, data: { breadcrumb: '  ', pageTitle: "  " } },
      { path: 'edit/:id', component: DefinedTestEditComponent, data: { breadcrumb: '  ', pageTitle: "  " } },
      { path: 'Assignment/Machine/:id/:Title', component: DefinedTestMachineAssignmentComponent, data: { breadcrumb: '    ', pageTitle: "   "} },
      { path: 'Assignment/edit/:id/:Title', component: DefinedTestMachineAssignmentEditComponent, data: { breadcrumb: '    ', pageTitle: "   "} },
      { path: 'Assignment/create/:id/:Title', component: DefinedTestMachineAssignmentEditComponent, data: { breadcrumb: '   ', pageTitle: "  "} },
      { path: 'Group/Assignment/:id/:Title', component: DefinedTestGroupAssignmentComponent, data: { breadcrumb: '    ', pageTitle: "   "} },
      { path: 'Group/Assignment/Create/:id/:Title', component: DefinedTestGroupAssignmentEditComponent, data: { breadcrumb: '    ', pageTitle: "   "} },
      { path: 'Group/Assignment/Edit/:id/:Title', component: DefinedTestGroupAssignmentEditComponent, data: { breadcrumb: '   ', pageTitle: "  "} },
    ], canActivate: [LoginGruard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TestRoutingModule { }
